:root {
  --font-size-default: 16px;
  --font-default: "Icarus";
  --font-bold-default: "Mario";
  --font-input: "Icarus";
  --font-dropdown: var(--font-input);
  --font-button: var(--font-input);
  /* color definitions */
  --color-font: var(--color-grey);
  --color-text: var(--color-font);
  --color-header: #ace398;
  --color-subheader: #fff;
  --color-error: var(--color-red);
  --color-error-dark: var(--color-red-dark);
  --input-color: var(--color-grey-1);
  --input-color-placeholder: var(--color-grey-4);
  --input-color-dark: var(--color-grey-9);
  --input-color-placeholder-dark: var(--color-grey-7);
  --button-font: "Mario";
  --button-font-size: 1rem;
  --button-text-color: var(--color-blue-light);
  --button-text-color-hover: white;
  --button-color-bg: none;
  --button-background-color: #0069d2;
  --button-height: 2rem;
  --button-radius: calc(var(--button-height) / 3.5);
  --button-border-width: 2px;
  --button-bar-height: var(--button-height);
  --caret-color: white;
  --caret-color-dark: var(--color-blue-dark);
  --input-padding: 0.75rem;
  --input-font-size: 1.25rem;
  --input-radius: 5px;
  --modal-radius: calc(2 * var(--button-radius));
  --modal-background: white;
  --modal-color-font: var(--color-grey-3);
  --header-height: 50px;
  --default-padding: 20px;
  --default-gutter: max(1rem, min(1vw, 3rem));
  --default-gutter: max(1rem, min(1vw, 3rem));
  --default-gutter-static: 15px;
  --gutter-large: max(2rem, min(2vw, 6rem));
  --dropdown-background: rgba(20, 38, 67, 1);
  --dropdown-border-color: #fff;
  --dropdown-border-width: 1px;
  --dropdown-shadow: 1px 1px 10px rgba(0, 0, 0, 0.25);
  --dropdown-shadow-light: 1px 0px 5px rgba(0, 0, 0, 0.1);
  --default-item-padding: 15px;
  --defaultPagePadding: 20px;
  --defaultPagePaddingTop: 50px;
  --defaultButtonPadding: 0.7em 1.1em;
  --defaultButtonHeight: 45px;
  --defaultInputPadding: 10px;
  --defaultRadius: 3px;
  --fontSizeInput: 1em;
  --animDefault: 0.35s;
  --color-red: #ff7158;
  --color-green: #66ffcc;
  --color-green-2: #3ec692;
  --color-green-3: #2af378;
  --color-blue-3: #89e0f4;
  --color-blue-2: #37c6f7;
  --color-blue: #3976ee;
  --color-purple: #623fea;
  --color-magenta: #a61fe5;
  --color-magenta-2: #f33fff;
  --color-orange: #eaa824;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-link: #90c3d4;
  --color-link-hover: #90c3d4;
  --color-font: #fff;
  --color-font-grey: #333;
  --color-background: #fff;
  --button-radius: var(--default-radius);
  --button-color-bg: var(--color-blue);
  --button-color-font: var(--color-white);
  --button-color-bg-hover: var(--color-blue);
  --button-text-color: var(--color-blue);
  --button-text-color-hover: white;
  --input-radius: var(--default-radius);
  --input-padding: 10px;
  --input-font-size: 1.18rem;
  --font-size-h1: 1rem;
  --font-size-h2: 1.2;
  --default-gutter: 1vw;
  /* Portrait and Lansdscape */
}
@media only screen and (min-device-width: 0px) and (max-device-width: 768px) and (-webkit-min-device-pixel-ratio: 2) {
  :root {
    --defaultPagePadding: 10px;
    --default-item-padding: 10px;
    --defaultRadius: 7px;
    --header-height: 85px;
    --default-gutter: 4vw;
    --default-gutter-static: 15px;
    --gutter-large: 8vw;
    --default-padding: 15px;
  }
}

.entry, .entry-list .entries .entry {
  padding: 20px;
  text-align: left;
  line-height: 1.5em;
  opacity: 0.8;
}
.entry .title, .entry-list .entries .entry .title {
  color: #ace398;
  font-size: 1.65em;
  line-height: 1.25em;
  font-weight: 900;
  font-family: "Mario";
  margin: 5px 0 10px;
}
.entry .date, .entry-list .entries .entry .date {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.75em;
}
.entry .content, .entry-list .entries .entry .content {
  color: white;
  margin-top: 5px;
}
.entry .content .markdown-content ul {
  padding-left: 5px;
  list-style: square;
  color: white;
}
.entry .content .markdown-content ul li {
  padding: 4px;
}
.entry .content .markdown-content ul li::before {
  position: relative;
  left: -5px;
  content: "•";
  color: rgba(255, 255, 255, 0.75);
}
.entry .content .markdown-content pre div {
  background: rgba(40, 44, 52, 0.2) !important;
}
.entry .content .markdown-content pre div code {
  text-shadow: 1px 1px 1px black !important;
  background: none !important;
  font-smooth: always;
}
.entry .content .markdown-content h1 {
  font-size: 1.35em;
  padding: 15px 0 10px;
  color: gold;
}
.entry .content .markdown-content h2 {
  font-size: 1.25em;
  padding: 10px 0 7px;
  color: gold;
}
.entry .content .markdown-content h3 {
  font-size: 1.15em;
  padding: 7px 0 5px;
  color: gold;
}
.entry .content .markdown-content h4 {
  font-size: 1.1em;
  padding: 5px 0 3px;
  color: gold;
}
.entry:hover, .entry-list .entries .entry:hover {
  opacity: 1;
  cursor: pointer;
}

.entry-list {
  display: flex;
  flex-direction: column;
}
.entry-list .entries {
  display: flex;
  flex-direction: column;
}
#entry .entry {
  opacity: 1;
  cursor: auto;
  cursor: initial;
}
