#work .sub-nav {
  padding: 0 10px 10px;
}
#work .sub-nav a {
  font-weight: bold;
  padding: 10px;
  font-family: "8bit";
  color: rgba(194.1071428571, 209.8214285714, 233.3928571429, 0.3490196078);
  letter-spacing: 2px;
  font-size: 1.25em;
  transition: color 0.1s linear;
}
#work .sub-nav a:hover {
  color: rgba(100, 140, 200, 0.3490196078);
  transition: color 0.1s linear;
}
#work .sub-nav a.on {
  color: #90c3d4;
  transition: color 0.1s linear;
  text-shadow: 0px 0px 15px rgba(235, 255, 220, 0.35);
}

@media all and (max-width: 1023px) {
  #work .sub-nav {
    width: 100%;
    text-align: center;
  }
}
