#other h3 {
  margin-bottom: 5px;
}
#other > h3 {
  margin-left: 20px;
}
#other h3.latest {
  margin-left: 20px;
  margin-bottom: 0;
}
#other h3.see-more {
  margin-bottom: 20px;
}
#other .bio {
  padding: 20px;
  border-radius: 12px;
  margin: 10px 0 20px;
  box-shadow: inset 1px 1px 26px linear-gradient(-30deg, #2f5047 0%, #99f2c8 100%);
  transition: all 0.35s ease;
}
#other .bio .left {
  min-width: 300px;
  padding-right: 10px;
}
#other .bio .left .col.image img {
  max-width: 180px;
  height: auto;
  border-radius: 4px;
}
#other .bio .left .col.me {
  padding: 0 10px 0 10px;
}
#other .bio .left .col.me h3:not(:first-child) {
  margin-top: 10px;
}
#other .bio .right {
  flex: 1;
  padding-left: 10px;
}
#other .bio .right .section:not(:last-child) {
  margin-bottom: 10px;
}
#other .bio .right .section ul li {
  display: block;
  margin-left: 5px;
}
#other .bio .right #skills .skill-types h4 {
  font-family: "Mario";
}
#other .bio .right #skills .skill-types h4:not(:first-child) {
  margin-top: 10px;
}
#other .bio .right #skills .skill-types ul {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
}
#other .bio .right #skills .skill-types ul li {
  display: block;
  padding: 0px 10px;
  font-size: 0.9em;
}

@media all and (max-width: 1023px) {
  #home .bio {
    border-radius: 0;
    width: 100%;
  }
}
@media all and (max-width: 767px) {
  #home .bio {
    display: block;
  }
  #home .bio .left,
  #home .bio .right {
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    padding-left: 0;
  }
  #home .bio .left .col.image img {
    max-width: 115px;
  }
}
